import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';

const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./stage-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class StageAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 stageId = 0;  
	 status;
	 stageForm: FormGroup;
	 stage= new StageData();  
	 bootstrapFormValues = [];
	 msg = [];
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.stageId).subscribe(params=>{
			if(params.stageId != undefined){
				this.stageId=params.stageId; 
				}
		}); 
	
	if(this.stageId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.stageForm = new FormGroup({			
			'stageId' : new FormControl(''),
			'stageName' : new FormControl('', [ Validators.required]),
			'stageProbability' : new FormControl('', [Validators.pattern(NUM_REGAX), Validators.required]),
			'stageEntryId' : new FormControl(''),
			'stageEntryDate' : new FormControl(''),
			'stageModifiedId' : new FormControl(''),
			'stageModifiedDate' : new FormControl(''),
		});
  }
	  
	    get stageName(){ return this.stageForm.get('stageName')};
	    get stageProbability(){ return this.stageForm.get('stageProbability')};
	    get stageEntryId(){ return this.stageForm.get('stageEntryId')};
	    get stageEntryDate(){ return this.stageForm.get('stageEntryDate')};
	    get stageModifiedId(){ return this.stageForm.get('stageModifiedId')};
	    get stageModifiedDate(){ return this.stageForm.get('stageModifiedDate')};

	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/stage-data',
									JSON.stringify({ stageId :this.stageId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.stage = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/stage-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.stage[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.stageForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.stageId == 0){ 
			this.stage['requestType'] = 'add';
		}else{
			this.stage['requestType'] = 'update';
			this.stage['stageId'] = this.stageId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/stage',
									JSON.stringify(this.stage),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.stageForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
			 console.log(this.stage);
			
			}
	  }


  onDelete(){
		 this.stage['requestType'] = 'delete';
		 this.stage['stageId'] = this.stageId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/stage',
									JSON.stringify(this.stage),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.stageForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
	 }
}

export class StageData {
	
	stageId: string ;
	stageName: string;
	stageProbability: string;
	stageEntryId:string;
	stageEntryBy:string;
	stageEntryDate:string;
    stageModifiedId:string;
 	stageModifiedBy:string;
    stageModifiedDate:string;
	
	 
}


